import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar';
import uploadImage from '@/components/common/upload-image';
export default {
	components:{
		uniNavBar,
		uploadImage
	},
	data() {
		return {
			content:"",
			imageList:[],
			showBack:false,
		}
	},
	// 监听返回
	onBackPress(){
		// return true
		if(!this.showBack && (this.content !== '' || this.imageList.length > 0)){
			uni.showModal({
				content: '是否要保存为草搞',
				showCancel: true,
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if (res.confirm){
						this.store()
					}else { //点击不保存，清楚缓存
						uni.removeStorage({
							key:'add-input'
						})
					}
					this.back();
				}
			});
			this.showBack = true;
			return true
		}
	},
	onLoad() {
		uni.getStorage({
			key:"add-input",
			success:(res) => {
				if(res.data){
					let obj = JSON.parse(res.data);
					this.content = obj.content;
					this.imageList = obj.imageList;
				}
				
			}
		})
	},
	computed:{
		show(){
			return this.imageList.length > 0
		}
	},
	methods: {
		// 选中图片
		changeImage(e){
			this.imageList = e;
		},
		// 返回上一步
		back() {
			uni.navigateBack({delta: 1})
		},
		store(){
			uni.setStorage({
				key:'add-input',
				data:JSON.stringify({
					content:this.content,
					imageList:this.imageList
				})
			})
		},
		// 底部图片点击事件
		iconClickEvent(e){
			switch (e){
				case "uploadImage":
				this.$refs.uploadImage.chooseImage()
					break;
				default:
					break;
			}
		}
	}
}
